"use client"

import { useParams, useRouter } from 'next/navigation'
import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { ArrowLeft, CheckCircle, Play } from 'lucide-react'
import { lessonDatabase } from '@/lib/japanese-data'

export default function LessonDetailPage() {
  const params = useParams()
  const router = useRouter()
  const lessonId = params.id as string
  
  const [lesson, setLesson] = useState<any>(null)
  const [currentStep, setCurrentStep] = useState(0)
  const [completedSteps, setCompletedSteps] = useState<number[]>([])
  const [isPlaying, setIsPlaying] = useState(false)

  useEffect(() => {
    const foundLesson = lessonDatabase.find(l => l.id === lessonId)
    if (foundLesson) {
      setLesson(foundLesson)
    } else {
      router.push('/')
    }
  }, [lessonId, router])

  if (!lesson) {
    return <div className="flex items-center justify-center h-screen">Loading...</div>
  }

  const steps = [
    { title: 'Introduction', content: lesson.content.introduction },
    { title: 'Key Points', content: lesson.content.keyPoints },
    { title: 'Examples', content: lesson.content.examples },
    { title: 'Practice', content: lesson.content.exercises }
  ]

  const handleStepComplete = () => {
    if (!completedSteps.includes(currentStep)) {
      setCompletedSteps([...completedSteps, currentStep])
    }
    
    if (currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1)
    }
  }

  const handlePreviousStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1)
    }
  }

  const playAudio = (text: string) => {
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(text)
      utterance.lang = 'ja-JP'
      utterance.rate = 0.8
      speechSynthesis.speak(utterance)
    }
  }

  const progress = ((completedSteps.length) / steps.length) * 100

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 to-blue-50 dark:from-gray-900 dark:to-gray-800">
      <div className="max-w-md mx-auto px-4 py-6">
        {/* Header */}
        <div className="flex items-center mb-6">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => router.push('/')}
            className="mr-4"
          >
            <ArrowLeft className="w-4 h-4" />
          </Button>
          <div>
            <h1 className="text-xl font-bold">{lesson.title}</h1>
            <p className="text-lg font-bold text-pink-600 dark:text-pink-400">{lesson.titleJapanese}</p>
          </div>
        </div>

        {/* Progress */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-base">Lesson Progress</CardTitle>
            <Progress value={progress} className="mt-2" />
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
              {completedSteps.length} of {steps.length} steps completed
            </p>
          </CardHeader>
        </Card>

        {/* Step Navigation */}
        <div className="flex justify-between mb-6">
          {steps.map((step, index) => (
            <div
              key={index}
              className={`flex-1 text-center px-2 py-1 rounded-lg text-xs ${
                index === currentStep
                  ? 'bg-blue-500 text-white'
                  : completedSteps.includes(index)
                  ? 'bg-green-500 text-white'
                  : 'bg-gray-200 dark:bg-gray-700'
              }`}
            >
              {step.title}
            </div>
          ))}
        </div>

        {/* Current Step Content */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-lg">{steps[currentStep].title}</CardTitle>
            <div className="flex gap-2">
              <Badge className="bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200">
                {lesson.difficulty}
              </Badge>
              <Badge className="bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                {lesson.jlptLevel}
              </Badge>
            </div>
          </CardHeader>
          
          <CardContent>
            {currentStep === 0 && (
              <div className="space-y-4">
                <p className="text-gray-700 dark:text-gray-300">{lesson.content.introduction}</p>
                <div className="text-sm text-gray-600 dark:text-gray-400">
                  Estimated time: {lesson.estimatedTime} minutes
                </div>
              </div>
            )}

            {currentStep === 1 && (
              <div className="space-y-3">
                <h3 className="font-semibold">Key Points to Remember:</h3>
                <ul className="space-y-2">
                  {lesson.content.keyPoints.map((point: string, index: number) => (
                    <li key={index} className="flex items-start">
                      <CheckCircle className="w-4 h-4 text-green-500 mr-2 mt-0.5 flex-shrink-0" />
                      <span className="text-sm">{point}</span>
                    </li>
                  ))}
                </ul>
              </div>
            )}

            {currentStep === 2 && (
              <div className="space-y-4">
                <h3 className="font-semibold">Examples:</h3>
                {lesson.content.examples.map((example: any, index: number) => (
                  <Card key={index} className="p-4">
                    <div className="space-y-2">
                      <div className="text-lg font-bold text-pink-600 dark:text-pink-400">
                        {example.japanese}
                      </div>
                      <div className="text-sm text-gray-600 dark:text-gray-400">
                        {example.hiragana}
                      </div>
                      <div className="text-sm">{example.english}</div>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => playAudio(example.japanese)}
                        className="mt-2"
                      >
                        <Play className="w-3 h-3 mr-1" />
                        Play Audio
                      </Button>
                    </div>
                  </Card>
                ))}
              </div>
            )}

            {currentStep === 3 && (
              <div className="space-y-4">
                <h3 className="font-semibold">Practice Exercises:</h3>
                {lesson.content.exercises.map((exercise: any, index: number) => (
                  <Card key={index} className="p-4">
                    <div className="space-y-3">
                      <p className="font-medium">{exercise.question}</p>
                      <div className="space-y-2">
                        {exercise.options.map((option: string, optIndex: number) => (
                          <div
                            key={optIndex}
                            className="p-2 border rounded-lg text-sm hover:bg-gray-50 dark:hover:bg-gray-800 cursor-pointer"
                          >
                            {option}
                          </div>
                        ))}
                      </div>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        {exercise.explanation}
                      </p>
                    </div>
                  </Card>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Navigation Buttons */}
        <div className="flex gap-4">
          <Button
            variant="outline"
            onClick={handlePreviousStep}
            disabled={currentStep === 0}
            className="flex-1"
          >
            Previous
          </Button>
          <Button
            onClick={handleStepComplete}
            disabled={currentStep === steps.length - 1}
            className="flex-1"
          >
            {currentStep === steps.length - 1 ? 'Complete Lesson' : 'Next Step'}
          </Button>
        </div>
      </div>
    </div>
  )
}
