// Japanese learning content and data

export interface JapaneseWord {
  id: string
  japanese: string
  hiragana: string
  romaji: string
  english: string
  category: string
  jlptLevel: 'N5' | 'N4' | 'N3' | 'N2' | 'N1'
  examples?: {
    japanese: string
    hiragana: string
    english: string
  }[]
}

export interface Lesson {
  id: string
  title: string
  titleJapanese: string
  description: string
  difficulty: 'Beginner' | 'Intermediate' | 'Advanced'
  jlptLevel: 'N5' | 'N4' | 'N3' | 'N2' | 'N1'
  category: 'hiragana' | 'katakana' | 'kanji' | 'grammar' | 'vocabulary' | 'culture'
  content: {
    introduction: string
    keyPoints: string[]
    examples: {
      japanese: string
      hiragana: string
      english: string
    }[]
    exercises: {
      question: string
      options: string[]
      correctAnswer: number
      explanation: string
    }[]
  }
  estimatedTime: number // in minutes
  prerequisites?: string[]
}

