// Helper functions
export const getVocabularyByCategory = (category: string): JapaneseWord[] => {
  return vocabularyDatabase.filter(word => word.category.toLowerCase() === category.toLowerCase())
}

export const getVocabularyByJLPT = (level: string): JapaneseWord[] => {
  return vocabularyDatabase.filter(word => word.jlptLevel === level)
}

export const getLessonsByCategory = (category: string): Lesson[] => {
  return lessonDatabase.filter(lesson => lesson.category === category)
}

export const getLessonsByDifficulty = (difficulty: string): Lesson[] => {
  return lessonDatabase.filter(lesson => lesson.difficulty === difficulty)
}

export const searchVocabulary = (query: string): JapaneseWord[] => {
  const lowercaseQuery = query.toLowerCase()
  return vocabularyDatabase.filter(word => 
    word.japanese.includes(query) ||
    word.hiragana.includes(query) ||
    word.romaji.toLowerCase().includes(lowercaseQuery) ||
    word.english.toLowerCase().includes(lowercaseQuery) ||
    word.category.toLowerCase().includes(lowercaseQuery)
  )
}
